.form-input .custom-select-opener {
  display: flex;
  align-items: center;
  height: 100%;
  padding-left: calc(var(--spacing) * 4);
}

.custom-select-container {
  width: 100%;
  background: none;
}
.custom-select-opener {
  background: none;
}
.custom-select-panel {
  border-radius: 6px;
  background: #fff;
  max-height: 10.7em !important;
  transition: 0.3s ease;
  border: 1px solid #CFCFCF;
