:root {
  --section-width: calc(100vw - 30px);
  --section-gap-top: 20px;
  --section-gap-bottom: 20px;
}

.container {
  width: 100%;
  max-width: var(--section-width);
  margin: 0 auto;
}

dialog,
section,
header,
footer {
  padding: 0px calc(50% - var(--section-width) / 2);
}

section {
  padding-top: var(--section-gap-top);
  padding-bottom: var(--section-gap-bottom);
}

@media (min-width: 679.98px) {
  :root {
    --section-width: 650px;
  }
}
@media (min-width: 1015.98px) {
  :root {
    --section-width: 1024px;
  }
