  border: none;
  outline: none;
  user-select: none;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  --size: 40px;
  height: var(--size);
  padding: 0 calc(var(--size) / 2);
  border-radius: var(--radius-small);
  font-size: 14px;
  font-family: var(--font-base);
  font-weight: bold;
  transition: 0.2s ease;
  color: var(--btn-color);
}
.btn:focus:not(:active) {
