  overflow: hidden;
}
.lk-tasks-table__start {
  min-width: 50px;
  width: 50px;
  display: flex;
  justify-content: center;
}
.lk-tasks-table__status {
  min-width: 90px;
  width: 90px;
  margin-right: 23px;
}
.lk-tasks-table__number {
  min-width: 58px;
  width: 58px;
}
.lk-tasks-table__desc {
  flex-grow: 1;
}
.lk-tasks-table__type {
  min-width: 145px;
  width: 145px;
}
.lk-tasks-table__date {
  min-width: 212px;
  width: 212px;
}
.lk-tasks-table__people {
  min-width: 102px;
  width: 102px;
  display: flex;
}
.lk-tasks-separator, .lk-tasks-row, .lk-tasks-header {
  display: flex;
  align-items: center;
}
.lk-tasks-header {
  padding-top: 15px;
  padding-bottom: 15px;
}
.lk-tasks-separator {
  padding-top: 6px;
  padding-bottom: 6px;
  background: #E2E8F0;
}
@media (hover) {
  .lk-tasks-separator:hover {
    background: #d1dbe7;
  }
}
.lk-tasks-separator__btn {
  width: 18px;
  height: 18px;
  background: #fff;
  border: 1px solid #CFCFCF;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.lk-tasks-row {
  padding-top: 10px;
  padding-bottom: 10px;
}
.lk-tasks__status {
  height: 22px;
}
.lk-tasks__status--color-icon {
  background: var(--color-icon-30);
  color: var(--color-icon);
}
.lk-tasks__status--yellow {
  background: var(--color-yellow-30);
  color: var(--color-yellow);
}
.lk-tasks__status--red {
  background: var(--color-red-30);
  color: var(--color-red);
}
.lk-tasks__status--primary {
  background: var(--color-primary-30);
  color: var(--color-primary);
}
.lk-tasks__status--green {
  background: var(--color-green-30);
  color: var(--color-green);
}
.lk-tasks__users {
  background: #FFFFFF;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.14);
  border-radius: 50px;
  padding: 8px 6px;
}

.lk-clients {
  --section-width: 1230px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
.lk-clients-wrapper {
  display: flex;
  flex-direction: column;
}
.lk-clients-inner {
  position: relative;
  flex-grow: 1;
}
.lk-clients-block {
  background: var(--bg3);
  border: 1px solid var(--color-text-10);
  box-sizing: border-box;
  border-radius: 16px;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.lk-clients-col {
  width: 16.6666666667%;
  padding: 8px;
  overflow: auto;
  transition: 0.2s;
}
.lk-clients-col.drop-zone--over {
  z-index: 2;
  box-shadow: 0 0 0 2px var(--color-primary);
}
.lk-clients-col + .lk-clients-col {
  border-left: 1px solid var(--color-text-10);
}
.lk-clients-col:first-of-type {
  border-radius: var(--radius-large) 0 0 var(--radius-large);
}
.lk-clients-col:last-of-type {
  border-radius: 0 var(--radius-large) var(--radius-large) 0;
}
.lk-clients-head {
  position: sticky;
  top: 0;
  z-index: 2;
  background: linear-gradient(var(--background-color), var(--background-color)), var(--bg3);
}
.lk-clients-item {
  background: var(--bg3);
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.14);
  border-radius: var(--radius-small);
}
.lk-clients-item.draggable {
  cursor: pointer;
  user-select: none;
}
@media (hover) {
  .lk-clients-item.draggable:hover {
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.24);
  }
}
.lk-clients-item.draggable-mirror {
  z-index: 5;
}
.lk-clients-item.draggable-source--is-dragging {
  opacity: 0.5;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.14), 0 0 0 2px var(--color-primary) !important;
}

.dialog-profile-edit__content {
  position: relative;
  padding-left: 14px;
}
.dialog-profile-edit__fields {
  width: 350px;
  max-width: 100%;
}
.dialog-profile-edit__top-img {
  display: block;
}
.dialog-profile-edit__top-img-btn {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(49, 49, 49, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
}
.dialog-profile-edit__top-img .filepond--root {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.dialog-profile-edit__avatar {
  border-width: 4px;
  border-color: var(--bg3);
  background: var(--bg3);
  left: 28px;
  display: grid;
}
.dialog-profile-edit__avatar img {
  grid-row: 1/-1;
  grid-column: 1/-1;
}
.dialog-profile-edit__avatar-btn {
  grid-row: 1/-1;
  grid-column: 1/-1;
  width: 100%;
  height: 100%;
  background: rgba(49, 49, 49, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
}